#app {
  height: 100%;
}

.swiper {
  margin-bottom: 80px;
  width: 90%;
  height: 100%;
}
.swiper__title {
  margin-bottom: 50px;
  padding: 0 15px;
}
.swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #fff;
  text-align: left;
  display: flex;
  justify-content: center;
  align-items: center;
}

.swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: contain;
}
.swiper__conteiner_img {
  padding: 10px;
  margin: 0 auto;
  height: 281px;
}
.swiper-button-next:after,
.swiper-button-prev:after {
  color: #725fee;
  padding: 5px 10px;
  border: 2px solid #725fee;
  border-radius: 10px;
}
.swiper-button-next,
.swiper-button-prev {
  top: 10px;
}

.swider_item_do {
  align-items: center;
  display: flex;
  width: 244px;
  justify-content: space-between;
}
.swider_item_do img {
  width: 20px;
}
.swiper-slide p {
  font-weight: 700;
  margin: 10px 0;
}

.product__btn {
  font-size: 20px;
  padding: 10px 30px;
  border-radius: 10px;
  border: none;
  color: white;
  background-color: #725fee;
}
.swiper__titles {
  min-height: 46px;
}
#slide {
  display: flex;
  flex-shrink: 0;
  width: 100%;
  height: 100%;
  position: relative;
  transition-property: transform;
  /* display: block; */
  align-items: center;
  flex-direction: column;
}
.swiper-container {
  position: relative; /* Относительное позиционирование для контейнера свайпера */
}

.swiper-button-next,
.swiper-button-prev {
  position: absolute; /* Абсолютное позиционирование для кнопок навигации */
  top: 300px;
  width: 30px; /* Ширина кнопок */
  height: 30px; /* Высота кнопок */
  background-color: rgba(255, 255, 255, 0.7); /* Цвет кнопок (пример) */
  border-radius: 50%; /* Закругляем углы для создания круглых кнопок (пример) */
  z-index: 10; /* Поднимаем кнопки над слайдами */
  cursor: pointer; /* Устанавливаем указатель при наведении */
}

.swiper-button-next {
  right: 15px; /* Отступ справа на 15px */
}

.swiper-button-prev {
  left: 15px; /* Отступ слева на 15px */
}
@media (max-width: 1250px) {
  .swiper__conteiner_img {
    height: 265px;
    padding: 29px;
  }
}
@media (max-width: 1250px) {
  .swiper__conteiner_img {
    height: 200px;
  }
  .swiper__titles {
    font-size: 15px;
    min-height: 0;
  }
  .swider_item_do {
    align-items: center;
    display: flex;
    width: 211px;
    justify-content: space-between;
  }
}
